'use client'

import Cookies from 'js-cookie'
import { Moon, Sun } from 'lucide-react'
import { usePathname, useRouter } from 'next/navigation'
import { useTheme } from 'next-themes'
import React, { useState } from 'react'
import ReactCountryFlag from 'react-country-flag'
import { useTranslation } from 'react-i18next'

import { Button } from '~/components/ui/button'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger
} from '~/components/ui/dropdown-menu'
import { Locale } from '~/lib/i18n/config'
import { cn } from '~/lib/utils'

const languages = [
  { code: 'en', name: 'English', countryCode: 'GB' },
  { code: 'ru', name: 'Русский', countryCode: 'RU' },
  { code: 'lv', name: 'Latviešu', countryCode: 'LV' },
  { code: 'nl', name: 'Nederlands', countryCode: 'NL' }
]

type Menu = 'theme' | 'lang' | null

export function ThemeAndLanguage({ locale }: { locale: Locale }) {
  const [openMenu, setOpenMenu] = useState<Menu>(null)
  const router = useRouter()
  const pathname = usePathname()
  const { setTheme } = useTheme()
  const { t } = useTranslation('common')

  const changeLanguage = (newLang: string) => {
    Cookies.set('NEXT_LOCALE', newLang, { expires: 365 })
    const segments = pathname?.split('/') || []
    segments[1] = newLang
    router.push(segments.join('/') || `/${newLang}`)
    setOpenMenu(null) // close after select
  }

  const handleTriggerClick = (menu: Menu) => {
    if (openMenu && openMenu !== menu) {
      // Switching directly from one menu to another
      setOpenMenu(null)
      requestAnimationFrame(() => setOpenMenu(menu))
    } else {
      // Toggle normally
      setOpenMenu(openMenu === menu ? null : menu)
    }
  }

  const currentLang = languages.find((l) => l.code === locale)

  return (
    <div className="flex items-center gap-4">
      {/* Theme menu */}
      <DropdownMenu
        open={openMenu === 'theme'}
        onOpenChange={(isOpen) => setOpenMenu(isOpen ? 'theme' : null)}
      >
        <DropdownMenuTrigger
          onPointerDown={(e) => {
            if (openMenu && openMenu !== 'theme') {
              // example for theme menu
              e.preventDefault() // prevent Radix's internal toggle
              setOpenMenu(null)
              requestAnimationFrame(() => setOpenMenu('theme'))
            }
          }}
          asChild
        >
          <Button variant="outline" size="icon" onClick={() => handleTriggerClick('theme')}>
            <Sun className="h-[1.2rem] w-[1.2rem] dark:hidden" />
            <Moon className="hidden h-[1.2rem] w-[1.2rem] dark:block" />
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="end">
          <DropdownMenuItem
            onClick={() => {
              setTheme('light')
              setOpenMenu(null)
            }}
          >
            {t('theme.light')}
          </DropdownMenuItem>
          <DropdownMenuItem
            onClick={() => {
              setTheme('dark')
              setOpenMenu(null)
            }}
          >
            {t('theme.dark')}
          </DropdownMenuItem>
          <DropdownMenuItem
            onClick={() => {
              setTheme('system')
              setOpenMenu(null)
            }}
          >
            {t('theme.system')}
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>

      {/* Language menu */}
      <DropdownMenu
        open={openMenu === 'lang'}
        onOpenChange={(isOpen) => setOpenMenu(isOpen ? 'lang' : null)}
      >
        <DropdownMenuTrigger asChild>
          <Button variant="outline" size="icon" onClick={() => handleTriggerClick('lang')}>
            {currentLang && (
              <ReactCountryFlag
                svg
                countryCode={currentLang.countryCode}
                className="rounded-lg text-2xl"
              />
            )}
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="end">
          {languages.map(({ code, countryCode, name }) => (
            <DropdownMenuItem
              key={code}
              onClick={() => changeLanguage(code)}
              className={cn('flex items-center gap-2', {
                'font-medium': code === locale
              })}
            >
              <ReactCountryFlag countryCode={countryCode} svg className="h-4 w-4" />
              {name}
            </DropdownMenuItem>
          ))}
        </DropdownMenuContent>
      </DropdownMenu>
    </div>
  )
}
